#cookiebar {
  position: fixed;
  z-index: 820511;
  right: 0;
  left: 0;
  background-color: rgba(#fff, .97);
  font-size: 12px;
  line-height: 1.2;
  color: #555;
  text-align: center;
  padding-right: 2em;
  padding-left: 2em;
  box-shadow: 0 0 4px 2px rgba(0,0,0,.1);

  .inner {
    margin-right: auto;
    margin-left: auto;

    > *:first-child:before {
      content: "";
      display: inline-block;
      width: 14px;
      height: 14px;
      background: url(../media/cookie.png) no-repeat scroll center center transparent;
      background-size: cover;
      vertical-align: middle;
      margin-top: -1px;
      margin-right: .56em;
      opacity: .55;
    }
  }

  p {
    margin: 1em 0;
  }

  a {
    text-decoration: underline;

    &:hover {
      text-decoration: none;
    }
  }

  &[data-position="top"] { top: 0; }
  &[data-position="bottom"] { bottom: 0; }
}